// store 
export function parGetData(v) {
	if (v.indexOf('obj-') === 0) {
		v = v.slice(4);
		return JSON.parse(v);
	} else {
		return v;
	}
}
// localstore 
export function parSetData(v) {
	if (typeof v == 'object') {
		v = JSON.stringify(v);
		v = 'obj-' + v;
	} 
	return v;
}

// 
function checklogin() {
	// openid
	var openid = uni.getStorageSync('wxopenid');
	openid = openid ? parGetData(openid) : '';
	// 
	var userinfo = uni.getStorageSync('userinfo');
	userinfo = userinfo ? parGetData(userinfo) : '';
	// tokenapi
	var token = uni.getStorageSync('token');
	token = token ? parGetData(token) : '';
	
	var appusername = uni.getStorageSync('appusername');
	appusername = appusername ? parGetData(appusername) : '';
	
	return {
		openid,
		userinfo,
		token,
		appusername
	}
}

// 
function logincomm(info, self) {

















	/*  */
	if (!info.detail || !info.detail.userInfo) {
		return false;
	}
	var userinfo = info.detail.userInfo;
	uni.showLoading({
		title: '',
		icon: 'none'
	})
	if (typeof self.showlogin !== 'undefined') {
		self.showlogin = false
	}
	uni.login({
		provider: 'weixin',
		success: function(res) {
			// codeopenid
			uni.request({
				url: apiurl + 'syj/index/getopenid',
				method: "POST",
				data: {
					code: res.code,
					nickname: userinfo.nickName,
					avatarurl: userinfo.avatarUrl,
					shebei: shebei
				},
				success: function(ret) {
					if (ret.statusCode == 200 && ret.data.openid) {
						var openid = ret.data.openid;
						userinfo.openid = openid;
						userinfo.avatar = userinfo.avatarUrl;
						self.openid = openid
						self.token = ret.data.token
						self.userinfo = userinfo
						// 
						uni.setStorageSync(
							'wxopenid',
							parSetData(openid)
						);
						// 
						uni.setStorageSync(
							'userinfo',
							parSetData(userinfo)
						)
						// token
						uni.setStorageSync(
							'token',
							parSetData(ret.data.token)
						)
						if (ret.data.username) {
							// app
							// appusername
							uni.setStorageSync(
								'appusername',
								parSetData(ret.data.username)
							)
							if (typeof self.appusername !== 'undefined') {
								self.appusername = ret.data.username
							}
						}
					}
				},
				complete: function() {
					uni.hideLoading()
				},
				header: {
					auth: httpauth
				}
			});
		}
	});

}


// 
function setlike(self, data, type, tid) {
	// self = vue object 
	// data = self.data self.list[index]
	// type = like or hate
	// tid = article id
	if (!self.token) {




			self.showlogin = true;
			return false;
	}
	if (data.type) {
		//  
		return false;
	} else {
		// +1
		if (type == 'like') {
			data.type = 'like'
			data.like += 1;
		} else {
			data.type = 'hate'
			data.hate += 1;
		}
	}
	// 
	uni.request({
		url: apiurl + 'syj/index/setlike',
		method: "POST",
		dataType: "json",
		data: {
			type: type,
			id: tid,
			token: self.token,
			shebei: shebei
		},
		header: {
			auth: httpauth
		}
	});
}


// arr
function concat(arr, obj) {
	Object.keys(obj).forEach(function(key) {
		arr.push(obj[key])
	});
	return arr;
}

export default {
	apiurl,
	shebei,
	appkey,
	httpauth,
	parSetData,
	parGetData,
	checklogin,
	logincomm,
	setlike,
	concat
};
